.talent-page {
  &.arabic {
    p {
      padding-left: unset;
      padding-right: 2rem;
    }
    .para .container img.hope {
      margin-right: 25%;
      margin-left: unset;
    }
    .para .container img.abdIcon {
      margin-right: 25%;
      margin-left: unset;
    }
  }
  .para {
    .container {
      h2 {
        margin-top: 5rem;
        &:first-of-type::before {
          background-image: url("../assets/images/channels/talentHbg.webp");
        }

        &:nth-of-type(2)::before {
          background: unset;
        }

        &:nth-of-type(3)::before {
          background-image: url("../assets/images/channels/talent/our talents BG-01.webp");
        }
        &:nth-of-type(4)::before {
          background-image: url("../assets/images/contact/background-01.webp");
          transform: translate(-35px, -50%) rotate(95deg);
        }
      }

      img {
        &.hope {
          width: 50%;
          margin-left: 25%;
        }
        &.abdIcon {
        }
      }

      .abdsect {
        .imgs {
          position: relative;
          &::before {
            content: "";
            position: absolute;
            height: 100px;
            width: 100px;
            transform: rotate(60deg);
            bottom: 50px;
            background: {
              image: url("../assets/images/all_pages/newBgs1.webp");
              size: contain;
              repeat: no-repeat;
            }
          }
          img {
            width: 50%;
            margin-left: 25%;
          }

          h4 {
            width: fit-content;
            padding: 5px 9px 0;
            position: absolute;
            user-select: none;
            top: 50%;
            left: 50%;
            transform: translate(-110%, 300%);
            background-color: #fdfe00;
            cursor: pointer;

            &:hover {
              color: var(--txt-clr);
              transform: translate(-110%, 300%) scale(1.2);
            }
          }
        }

        p {
          padding-left: 2rem;

          &.bold {
            margin: 2rem 0;
          }

          &.withBg::before {
            width: 70px;
          }
        }

        .swp {
          margin-top: 0;
        }

        .tin:last-of-type {
          position: relative;

          &::before {
            content: "";
            background: {
              image: url(../assets/images/all_pages/newBgs1.webp);
              size: contain;
              repeat: no-repeat;
            }
            position: absolute;
            z-index: -1;
          }

          &::before {
            height: 100px;
            width: 100px;
            bottom: -50px;
            right: 0;
            transform: rotate(45deg);
            background-image: url(../assets/images/all_pages/newBgs1.webp);
          }
        }
      }
    }
  }

  .contact-form {
    .container {
      .box {
        @media (min-width: 577px) and (min-width: 1200px) {
          top: 160px !important;
        }
      }

      .box .form input.specBtn {
        background-color: white !important;
      }
    }
  }
}

.talent-page.arabic {
  .para .container .abdsect .tin:last-of-type {
    direction: rtl;

    &::before {
      left: 0;
      right: unset;
    }

    &::after {
      right: 0;
      left: unset;
    }
  }
}
